import React, { Fragment, useCallback, useState } from 'react';
import styled from 'styled-components';
import {
  IconButton, Modal,
} from '@mui/material';
import {
  FormikProvider, useFormik,
} from 'formik';
import * as Yup from 'yup';
import ArrowBackIosNewIcon from '@mui/icons-material/ArrowBackIosNew';
import { rgba } from 'polished';
import DeleteOutlineIcon from '@mui/icons-material/DeleteOutline';
import TextInput from '../../controls/TextInput';
import Button from '../../controls/Button';
import ErrorBlock from '../../controls/ErrorBlock';

const Wrap = styled.div`
  width: 100%;
`;

const Btn = styled(Button)`
  width: 100%;
`;

const Popup = styled(Modal)`
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  flex-wrap: nowrap;
  
  & .MuiBackdrop-root {
    background: #F8F8F8;
  }
`;

const Container = styled.div`
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  flex-wrap: nowrap;
`;

const ContentWrap = styled.div`
  width: 100vw;
  background: #FFFFFF;
  display: flex;
  flex-direction: column;
  align-items: center;
  flex-wrap: nowrap;
  height: calc(100vh - 157px);
  overflow-x: auto;
`;

const Content = styled.div`
  width: 100%;
  max-width: 450px;
  display: flex;
  flex-direction: column;
  padding: 16px;
`;

const Header = styled.div`
  width: 100%;
  height: 64px;
  max-width: 450px;
  display: flex;
  align-items: center;
  gap: 20px;
  padding: 16px;
`;

const BackIcon = styled(ArrowBackIosNewIcon)`
  width: 16px;
  height: 16px;
`;

const Title = styled.span`
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  color: #2E3135;
`;

const FieldsWrap = styled.div`
  display: flex;
  flex-direction: column;
  width: 100%;
`;

const MenuItem = styled.div`
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  background: ${(props) => (props.checked ? '#E6EBF9' : '#F8F8F8')};
  border-radius: 16px;
  padding: 12px 16px;
  cursor: pointer;
  margin-bottom: 24px;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  color: #2E3135;
  transition: background 250ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;

  &:hover {
    background: ${(props) => (props.checked ? rgba('#d2d8ea', 0.85) : '#f0f0f0')};
  }
`;

const ItemName = styled.span`
  font-weight: 600;
  font-size: 16px;
  line-height: 24px;
  margin-bottom: 16px;

  color: #2E3135;
`;

const Description = styled.span`
  font-weight: 600;
  font-size: 16px;
  line-height: 24px;
  margin-bottom: 16px;
  align-self: flex-start;
  padding: 16px 16px 0;
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;

  color: #2E3135;
`;

const FieldGroup = styled.div`
  display: flex;
  flex-direction: column;
  width: 100%;
`;

const SaveBtn = styled(Button)`
  &.MuiButtonBase-root {
    width: calc(100% - (16px * 2));
    position: fixed;
    bottom: 16px;
    max-width: 450px;
    left: 50%;
    transform: translateX(-50%);
  }
`;

const DeleteIcon = styled(DeleteOutlineIcon)`
  color: #ff1744;
  &.MuiDivider-root {
    width: 100%;
    border-color: #F8F8F8;
  }
`;

const ErrorPanel = styled.span`
  font-size: 14px;
  line-height: 16px;
  height: 28px;
  padding: 6px 16px;
  color: #ff1744;
  display: block;
`;

const ButtonWrap = styled.div`
  width: 100%;
  max-width: 450px;
  display: flex;
  flex-direction: column;
  padding: 16px;
`;

function TrafficBlock({ onClose, data, onChange }) {
  const formik = useFormik({
    initialValues: data || {
      quantityOfUsers: '',
      firstPurchaseConversion: '',
      numberOfSales: '',
      orders: '',
      incomeFromPerson: '',
      averageCheck: '',
      costs: '',
      ordersForBillingPeriod: '',
      cac: '',
      userCost: '',
      userRevenue: '',
      profit: '',
      grossRevenue: '',
      attractionCosts: '',
      fixedCosts: '',
    },
    validationSchema: Yup.object().shape({
      quantityOfUsers: Yup.number()
        .typeError('  ')
        .required(' '),
      firstPurchaseConversion: Yup.number()
        .typeError('  ')
        .required(' '),
      numberOfSales: Yup.number()
        .typeError('  ')
        .required(' '),
      orders: Yup.number()
        .typeError('  ')
        .required(' '),
      incomeFromPerson: Yup.number()
        .typeError('  ')
        .required(' '),
      averageCheck: Yup.number()
        .typeError('  ')
        .required(' '),
      costs: Yup.number()
        .typeError('  ')
        .required(' '),
      ordersForBillingPeriod: Yup.number()
        .typeError('  ')
        .required(' '),
      cac: Yup.number()
        .typeError('  ')
        .required(' '),
      userCost: Yup.number()
        .typeError('  ')
        .required(' '),
      userRevenue: Yup.number()
        .typeError('  ')
        .required(' '),
      profit: Yup.number()
        .typeError('  ')
        .required(' '),
      grossRevenue: Yup.number()
        .typeError('  ')
        .required(' '),
      attractionCosts: Yup.number()
        .typeError('  ')
        .required(' '),
      fixedCosts: Yup.number()
        .typeError('  ')
        .required(' '),
    }),
    onSubmit: (values) => {
      onChange({ traffic: values });
      formik.resetForm();
      onClose();
    },
  });

  const onSubmit = useCallback((e) => {
    e.preventDefault();
    formik.handleSubmit();
  }, [formik]);

  return (
    <FormikProvider value={formik}>
      <Header>
        <IconButton size="small" onClick={onClose}>
          <BackIcon fontSize="small" />
        </IconButton>
        <Title></Title>
      </Header>
      <ContentWrap>
        <Content>
          <FieldGroup>
            <FieldsWrap>
              <ItemName>  ()</ItemName>
              <TextInput
                placeholder=" "
                name="quantityOfUsers"
                value={formik.values.quantityOfUsers}
                onChange={formik.handleChange}
              />
              <ErrorBlock name="quantityOfUsers" />
            </FieldsWrap>

            <FieldsWrap>
              <ItemName>    (1)</ItemName>
              <TextInput
                placeholder=" "
                name="firstPurchaseConversion"
                value={formik.values.firstPurchaseConversion}
                onChange={formik.handleChange}
              />
              <ErrorBlock name="firstPurchaseConversion" />
            </FieldsWrap>

            <FieldsWrap>
              <ItemName> ()</ItemName>
              <TextInput
                placeholder=" "
                name="numberOfSales"
                value={formik.values.numberOfSales}
                onChange={formik.handleChange}
              />
              <ErrorBlock name="numberOfSales" />
            </FieldsWrap>

            <FieldsWrap>
              <ItemName></ItemName>
              <TextInput
                placeholder=" "
                name="orders"
                value={formik.values.orders}
                onChange={formik.handleChange}
              />
              <ErrorBlock name="orders" />
            </FieldsWrap>

            <FieldsWrap>
              <ItemName>   </ItemName>
              <TextInput
                placeholder=" "
                name="incomeFromPerson"
                value={formik.values.incomeFromPerson}
                onChange={formik.handleChange}
              />
              <ErrorBlock name="incomeFromPerson" />
            </FieldsWrap>

            <FieldsWrap>
              <ItemName> </ItemName>
              <TextInput
                placeholder=" "
                name="averageCheck"
                value={formik.values.averageCheck}
                onChange={formik.handleChange}
              />
              <ErrorBlock name="averageCheck" />
            </FieldsWrap>

            <FieldsWrap>
              <ItemName></ItemName>
              <TextInput
                placeholder=" "
                name="costs"
                value={formik.values.costs}
                onChange={formik.handleChange}
              />
              <ErrorBlock name="costs" />
            </FieldsWrap>

            <FieldsWrap>
              <ItemName>   </ItemName>
              <TextInput
                placeholder=" "
                name="ordersForBillingPeriod"
                value={formik.values.ordersForBillingPeriod}
                onChange={formik.handleChange}
              />
              <ErrorBlock name="ordersForBillingPeriod" />
            </FieldsWrap>

            <FieldsWrap>
              <ItemName>CAC</ItemName>
              <TextInput
                placeholder=" "
                name="cac"
                value={formik.values.cac}
                onChange={formik.handleChange}
              />
              <ErrorBlock name="cac" />
            </FieldsWrap>

            <FieldsWrap>
              <ItemName> </ItemName>
              <TextInput
                placeholder=" "
                name="userCost"
                value={formik.values.userCost}
                onChange={formik.handleChange}
              />
              <ErrorBlock name="userCost" />
            </FieldsWrap>

            <FieldsWrap>
              <ItemName>  </ItemName>
              <TextInput
                placeholder=" "
                name="userRevenue"
                value={formik.values.userRevenue}
                onChange={formik.handleChange}
              />
              <ErrorBlock name="userRevenue" />
            </FieldsWrap>

            <FieldsWrap>
              <ItemName></ItemName>
              <TextInput
                placeholder=" "
                name="profit"
                value={formik.values.profit}
                onChange={formik.handleChange}
              />
              <ErrorBlock name="profit" />
            </FieldsWrap>

            <FieldsWrap>
              <ItemName> </ItemName>
              <TextInput
                placeholder=" "
                name="grossRevenue"
                value={formik.values.grossRevenue}
                onChange={formik.handleChange}
              />
              <ErrorBlock name="grossRevenue" />
            </FieldsWrap>

            <FieldsWrap>
              <ItemName>  </ItemName>
              <TextInput
                placeholder=" "
                name="attractionCosts"
                value={formik.values.attractionCosts}
                onChange={formik.handleChange}
              />
              <ErrorBlock name="attractionCosts" />
            </FieldsWrap>

            <FieldsWrap>
              <ItemName> ,    </ItemName>
              <TextInput
                placeholder=" "
                name="fixedCosts"
                value={formik.values.fixedCosts}
                onChange={formik.handleChange}
              />
              <ErrorBlock name="fixedCosts" />
            </FieldsWrap>
          </FieldGroup>
        </Content>
      </ContentWrap>

      <SaveBtn type="submit" onClick={onSubmit}></SaveBtn>
    </FormikProvider>
  );
}

