function UnitEconomyActionBlock({
  // eslint-disable-next-line no-unused-vars
  actionName, onChange,
}) {
  const [open, setOpen] = useState(false);
  const [data, setData] = useState([{
    traffic: null, expenses: null, income: null,
  }]);
  const [error, setError] = useState(false);
  const [block, setBlock] = useState(null);
  const [index, setIndex] = useState(null);

  const handleOpen = useCallback(() => {
    setOpen(true);
  }, [setOpen]);

  const handleClose = useCallback(() => {
    setOpen(false);
  }, [setOpen]);

  const changeBlock = (blockName, newIndex) => () => {
    setBlock(blockName);
    setIndex(newIndex);
    setError(false);
  };

  const onChangeData = (partData) => {
    const newData = [...data];
    newData[index] = {
      ...newData[index],
      ...partData,
    };
    setData(newData);
  };

  const addItem = () => {
    setData([...data, {
      traffic: null, expenses: null, income: null,
    }]);
  };

  const deleteItem = (i) => {
    const newData = [...data];
    newData.splice(i, 1);
    setData(newData);
  };

  const onSave = useCallback(() => {
    const isError = data.some(({ traffic, income, expenses }) => !traffic || !expenses || !income);
    if (isError) {
      setError(true);
      return;
    }

    onChange({ [actionName]: data });
    handleClose();
    setIndex(null);
  }, [handleClose, onChange, data, actionName]);

  return (
    <Wrap>
      <Btn onClick={handleOpen}></Btn>
      <Popup
        open={open}
      >
        <Container>
          {!block ? (
            <>
              <Header>
                <IconButton size="small" onClick={handleClose}>
                  <BackIcon fontSize="small" />
                </IconButton>
                <Title>   </Title>
              </Header>
              <ContentWrap>
                {data.map((item, i) => (
                  // eslint-disable-next-line react/no-array-index-key
                  <Fragment key={i}>
                    <Description>
                      <span>
                        
                        {' '}
                        {i + 1}
                      </span>

                      <IconButton onClick={() => deleteItem(index)}>
                        <DeleteIcon />
                      </IconButton>
                    </Description>
                    <Content>
                      <MenuItem checked={item.traffic !== null} onClick={changeBlock('traffic', i)}>
                        <span></span>
                        <svg width="16" height="16" viewBox="0 0 16 16" fill="none" xmlns="http://www.w3.org/2000/svg">
                          <path d="M5 14L11 8L5 2" stroke="#2E3135" />
                        </svg>
                      </MenuItem>
                      <MenuItem checked={item.expenses !== null} onClick={changeBlock('expenses', i)}>
                        <span></span>
                        <svg width="16" height="16" viewBox="0 0 16 16" fill="none" xmlns="http://www.w3.org/2000/svg">
                          <path d="M5 14L11 8L5 2" stroke="#2E3135" />
                        </svg>
                      </MenuItem>
                      <MenuItem checked={item.income !== null} onClick={changeBlock('income', i)}>
                        <span></span>
                        <svg width="16" height="16" viewBox="0 0 16 16" fill="none" xmlns="http://www.w3.org/2000/svg">
                          <path d="M5 14L11 8L5 2" stroke="#2E3135" />
                        </svg>
                      </MenuItem>
                    </Content>
                  </Fragment>
                ))}
                <ButtonWrap>
                  <Button color="secondary" onClick={addItem}> </Button>
                </ButtonWrap>
              </ContentWrap>

              <ErrorPanel>{error ? '  ' : ''}</ErrorPanel>

              <SaveBtn type="submit" onClick={onSave}></SaveBtn>
            </>
          ) : null}

          {block === 'traffic' ? (
            <TrafficBlock
              onClose={changeBlock(null)}
              onChange={onChangeData}
              data={data[index].traffic}
            />
          ) : null}

          {block === 'expenses' ? (
            <ExpensesBlock
              onClose={changeBlock(null)}
              onChange={onChangeData}
              data={data[index].expenses}
            />
          ) : null}

          {block === 'income' ? (
            <IncomeBlock
              onClose={changeBlock(null)}
              onChange={onChangeData}
              data={data[index].income}
            />
          ) : null}
        </Container>
      </Popup>
    </Wrap>
  );
}

export default UnitEconomyActionBlock;
