import * as Yup from 'yup';

const requiredMsg = ' ';
const emailMsg = '  email';
const passwordMsg = ' 6 ,     ';
const passwordConfirmationMsg = '  ';
const minSymbolsMsg = (min) => `   - ${min}`;

// eslint-disable-next-line import/prefer-default-export
export const loginValidationSchema = Yup.object().shape({
  email: Yup.string().email(emailMsg).required(requiredMsg),
  password: Yup.string()
    .min(6, minSymbolsMsg(6))
    .required(requiredMsg),
});

export const registrationValidationSchema = Yup.object().shape({
  email: Yup.string().email(emailMsg).required(requiredMsg),
  password: Yup.string()
    .required(requiredMsg)
    .matches(
      /^(?=.*[a-zA-Z])(?=.*[0-9])(?=.{6,})/,
      passwordMsg,
    ),
  passwordConfirmation: Yup.string()
    .required(requiredMsg)
    .oneOf([Yup.ref('password'), null], passwordConfirmationMsg),
});
